html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

.main-nav {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    height: 64px;
    position: sticky;
    background: white;
    top: 0px;
    z-index: 100;
    border-bottom: 1px solid rgb(182, 182, 182);
}

#main-nav-left {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    margin-left: 100px;
    z-index: 100;
}

#main-nav-left > svg {
    margin-right: 20px;
    z-index: 100;
}

#main-nav-right {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-right: 100px;
    z-index: 100;
}

.search-top {
    height: 60%;
    width: 150px;
    background: #F6F6F6;
    border-radius: 5px;
    border: 0px;
    margin-right: 20px;
    display: relative;
    overflow: hidden;
    z-index: 100;
}

.search-top > input {
    display: absolute;
    height: 100%;
    background: #F6F6F6;
    border: 0px;
    width: 100%;
    z-index: 100;
}

#main-nav-right > div > span {
    display: absolute;
    right: 10px;
    top: 10px;
    z-index: 100;
}

.dashboard {
    font-size: 16px;
    font-weight: 600;
    background: white;
    border: 0.2px solid rgb(203, 203, 203);
    border-radius: 5px;
    padding: 5px 12px;
    z-index: 100;
}

.user-icons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    z-index: 100;
}

.user-icons {
    margin: 10px;
    height: 70%;
    z-index: 100;
}

.user-icon {
    margin: 10px 10px;
    z-index: 100;
}